body {
    background-color: #577696;
    overflow: hidden;
    width: 100vw;
    height: 100vh;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#root{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
}
#root > div{
    margin: 0 auto;
    padding: 10px;
    box-sizing: border-box;
    border: 1px solid #000;
    max-width: 500px;
    max-height: 500px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}
#root > div > ul{
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    margin: 0;
    padding: 0 0 5px 0;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}
#root > div > ul::-webkit-scrollbar{
    width: 5px;
}
#root > div > ul::-webkit-scrollbar-track{
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
}
#root > div > ul::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
}
#root > div > ul > li{
    margin: 3px 5px;
    list-style: none;
    padding: 5px 10px;
    font-size: 14px;
    border: 1px solid #000;
    border-radius: 15px;
}
#root > div > ul > li > p{
    word-wrap: break-word;
    overflow: hidden;
    margin: 0;
}
#root > div > ul > li.connected {
    align-self: center;
    font-size: 12px;
    border: none;
}
#root > div > ul > li.notMyMessage {
    align-self: flex-start;
}
#root > div > ul > li.myMessage{
    align-self: flex-end;
}
#root > div > form{
    display: flex;
    padding: 5px;
    box-sizing: border-box;
    height: 40px;
}
#root > div > form > input{
    width: 100%;
    outline: none;
    color: #000;
    padding-left: 10px;
    background-color: #0000;
    border: 1px solid #000;
    border-radius: 35px 0 0 35px;
}
#root > div > form > input::placeholder{
    color: #000;
}
#root > div > form > button{
    cursor: pointer;
    outline: none;
    font-size: 18px;
    border: none;
    background-color: #ffffff00;
    width: 50px;
    border: 1px solid;
    border-left: none;
    border-radius: 0 35px 35px 0;
}
#root > div > form > button:hover{
    background-color: #72707373;
}
